.gallery {
   display: flex;
   flex-flow: row wrap;
   justify-content: center;

   .gallery-menu {
      width: 100%;

      .image {
	 max-width: 100%;
	 max-height: 400px;
      }

      img {
	 display: block;
	 transition: filter .2s ease-in-out;
	 &:hover { filter: brightness(120%) }
      }
   }
}
@media screen and (min-width: 600px) {
   .gallery {
	 width: calc(90% / 2 );
   }
}

@media screen and (min-width: 1000px) {
   .gallery {
	 width: calc(90% / 3 );
   }
}
